import {
	highLightMsg
} from '../../utils/high-light.js'
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			searchText: '',
			historyList: uni.getStorageSync('search_commodity_cache'),
			wantList: ['手机', '考研', '篮球', '资料', ],
			inputTimeStamp: 0,
			inputTips: []
		}
	},
	watch: {},
	computed: {
		...mapState({
			location: state => state.UserLocation
		}),
		style() {
			var StatusBar = this.StatusBar;
			var CustomBar = this.CustomBar;
			var bgImage = this.bgImage;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});`;
			}
			return style
		}
	},
	mounted() {

	},
	methods: {
		quickSearch(keyword) {
			this.$store.dispatch('setSearchCommodity', keyword);
			uni.navigateBack({
				delta: 1
			});
		},
		searchTips(event) {
			this.inputTimeStamp = event.timeStamp;
			setTimeout(() => {
				//1s后比较二者是否还相同（因为只要还有事件触发，inputTimeStamp就会被改写，不再是当前事件函数的时间戳）
				if (this.inputTimeStamp == event.timeStamp) {
					this.requestTips();
				}
			}, 600);
		},
		requestTips() {
			this.inputTips = [];
			qqmapsdk.getSuggestion({
				keyword: this.searchText,
				region: this.location.user_address_component.city,
				success: res => {
					for (var i = 0; i < res.data.length; i++) {
						this.inputTips.push({
							title: res.data[i].title,
							id: res.data[i].id,
							addr: res.data[i].address,
							city: res.data[i].city,
							district: res.data[i].district,
							latitude: res.data[i].location.lat,
							longitude: res.data[i].location.lng
						});
					}
				}
			});
		},
		searchStart() {
			if (this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			} else {
				this.setSerachStorage();
				this.setSearch();
			}
		},
		clearHistory() {
			this.historyList = [];
			uni.setStorage({
				key: 'search_commodity_cache',
				data: []
			});
		},
		setSerachStorage() {
			let _this = this;
			uni.getStorage({
				key: 'search_commodity_cache',
				success(res) {
					let list = res.data;
					console.log(list);
					if (list.length > 9) {
						for (let item of list) {
							if (item == _this.searchText) {
								return false;
							}
						}
						list.pop();
						list.unshift(_this.searchText);
					} else {
						for (let item of list) {
							if (item == _this.searchText) {
								return false;
							}
						}
						list.unshift(_this.searchText);
					}
					_this.historyList = list;
					uni.setStorage({
						key: 'search_commodity_cache',
						data: _this.historyList
					});
				},
				fail() {
					_this.historyList = [];
					_this.historyList.push(_this.searchText);
					uni.setStorage({
						key: 'search_commodity_cache',
						data: _this.historyList
					});
				}
			})
		},
		highLight(item, highLight) {
			return highLightMsg(item, highLight)
		},
		setSearch() {
			if (this.searchText != '') {
				this.$store.dispatch('setSearchCommodity', this.searchText);
			}
			this.BackPage();
		},
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		}
	},
}
